.header {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 130px;
    max-width: 1200px;
    width: 100%;
    padding: 0px 20px;
    box-sizing: border-box;

    .btn-menu {
        display: none;
        color: #fff;
        background-color: transparent;
    }

    .header-content {
        display: flex;
        align-items: center;

        .item {
            color: #fff;
            font-size: 16px;
            line-height: 19px;
        }

        .item + .item {
            margin-left: 27px;
        }
    }

    .left-nav {
        &::after {
            content: '';
            position: fixed;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            background-color: rgba(0, 0, 0, 0.5);
        }
    }

    .btn-dashboard {
        width: 138px;
        height: 44px;
        border-radius: 64px;
        background-color: var(--main-color2);
        color: #fff;
        font-size: 16px;
        line-height: 19px;
    }

    .btn-torohub {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background-color: transparent;

        img {
            height: 44px;
        }
    }
}

@media only screen and (max-width: 1000px) {
    .header {
        .btn-menu {
            display: flex;
        }

        .header-content {
            display: none;
        }

        .left-nav {
        }
    }
}